<template>
    <div class="cargo-calculations-container">
      <h2>Расчёт количества коробок на паллете</h2>
      <v-divider></v-divider>
      <div class="my-calculate-form">
        <div class="inputs">
          <v-number-input
              class="max-pallet-height inpt"
              density="compact"
              :reverse="false"
              controlVariant="split"
              label="Высота палеты (мм)"
              :hideInput="false"
              :inset="false"
              :precision="2"
              variant="solo-filled"
              clearable
              v-model="maxPalletHeight"
          ></v-number-input>

          <v-number-input
              class="box-length inpt"
              density="compact"
              :reverse="false"
              controlVariant="split"
              label="Длинна коробки (мм)"
              :hideInput="false"
              :inset="false"
              :precision="2"
              variant="solo-filled"
              clearable
              v-model="boxLength"
          ></v-number-input>

          <v-number-input
              class="box-width inpt"
              density="compact"
              :reverse="false"
              controlVariant="split"
              label="Ширина коробки (мм)"
              :hideInput="false"
              :inset="false"
              :precision="2"
              variant="solo-filled"
              clearable
              v-model="boxWidth"
          ></v-number-input>

          <v-number-input
              class="box-height inpt"
              density="compact"
              :reverse="false"
              controlVariant="split"
              label="Высота коробки (мм)"
              :hideInput="false"
              :inset="false"
              :precision="2"
              variant="solo-filled"
              clearable
              v-model="boxHeight"
          ></v-number-input>

          <v-number-input
              class="box-weight inpt"
              density="compact"
              :reverse="false"
              controlVariant="split"
              label="Вес коробки (кг)"
              :hideInput="false"
              :inset="false"
              :precision="2"
              variant="solo-filled"
              clearable
              v-model="boxWeight"
          ></v-number-input>
        </div>
        <v-btn
            class="apply-calculations"
            color="green-darken-1"
            @click="checkFields"
        >Расчитать</v-btn>
        <v-btn
            class="apply-calculations"
            color="pink-accent-3"
            @click="clearCalculations"
        >Очистить</v-btn>
      </div>
      <v-divider v-if="isCalculated"></v-divider>

      <div class="results" v-if="isCalculated">
        <h4 class="optimal-title" >ОПТИМАЛЬНЫЙ ВАРИАНТ УКЛАДКИ</h4>
        <div class="optimal-result">

          <div class="best-wrapper">
            <div class="wrapper">
              <div class="info">Ориентация:</div>
              <div class="data">{{this.bestResult.best.orientation}}</div>
            </div>

            <div class="wrapper">
              <div class="info">Стабильность:</div>
              <div class="data">{{this.bestResult.best.stability}}</div>
            </div>

            <div class="wrapper">
              <div class="info">Ящиков в слое:</div>
              <div class="data">{{this.bestResult.best.boxesPerLayer}} ({{this.bestResult.best.layerDetails}})</div>
            </div>

            <div class="wrapper">
              <div class="info">Количество слоев:</div>
              <div class="data">{{this.bestResult.best.layers}}</div>
            </div>

            <div class="wrapper">
              <div class="info">Всего ящиков на паллете:</div>
              <div class="data">{{this.bestResult.best.totalBoxes}} шт.</div>
            </div>

            <div class="wrapper">
              <div class="info">Общий вес паллеты:</div>
              <div class="data">{{this.bestResult.best.totalWeight}} кг</div>
            </div>

            <div class="wrapper">
              <div class="info">Итоговая высота:</div>
              <div class="data">{{this.bestResult.best.totalHeight}} мм</div>
            </div>
          </div>
        </div>

        <h4 class="all-title">ВСЕ ВАРИАНТЫ (по убыванию эффективности)</h4>
        <div class="all-result" v-for="(result,index) in allResults" :key="index">

          <div class="all-result-count">
            <div class="variant"># {{index+1}}</div>
            <div class="variant-logo">
              <v-icon :color="result.stability === 'высокая' ? 'green' : result.stability === 'средняя' ? 'orange' : result.stability === 'низкая' ? 'red' : 'grey'"
                      size="large">mdi-checkbox-marked-circle</v-icon>
            </div>
          </div>

          <div class="best-wrapper">
            <div class="wrapper">
              <div class="info">Ориентация:</div>
              <div class="data">{{result.orientation}}</div>
            </div>

            <div class="wrapper">
              <div class="info">Стабильность:</div>
              <div class="data">{{result.stability}}</div>
            </div>

            <div class="wrapper">
              <div class="info">Ящиков в слое:</div>
              <div class="data">{{result.boxesPerLayer}} ({{result.layerDetails}})</div>
            </div>

            <div class="wrapper">
              <div class="info">Количество слоев:</div>
              <div class="data">{{result.layers}}</div>
            </div>

            <div class="wrapper">
              <div class="info">Всего ящиков на паллете:</div>
              <div class="data">{{result.totalBoxes}} шт.</div>
            </div>

            <div class="wrapper">
              <div class="info">Общий вес паллеты:</div>
              <div class="data">{{result.totalWeight}} кг</div>
            </div>

            <div class="wrapper">
              <div class="info">Итоговая высота:</div>
              <div class="data">{{result.totalHeight}} мм</div>
            </div>
          </div>
        </div>
      </div>
    </div>

</template>

<script>
export default {
  name: "LibraryComponent",
  data(){
    return {
      maxPalletHeight: null,
      boxLength: null,
      boxWidth: null,
      boxHeight: null,
      boxWeight: null,
      allowUnstable: false,

      isCalculated: false,
      calculationsResult: '',

      bestResult: {},
      allResults: []
    }
  },
  methods: {
    checkFields(){
      if (!this.maxPalletHeight || !this.boxLength || !this.boxWidth || !this.boxHeight || !this.boxWeight) {
        console.log('error');
      } else {
        this.calculate();
      }
    },
    calculate(){

// Статичные данные паллеты
      const PALLET = {
        length: 1200, // мм
        width: 800,   // мм
        height: 150   // мм
      };

      /**
       * Рассчитывает простую укладку (все ящики в одной ориентации)
       * @param {number} dim1 - первое измерение ящика (мм)
       * @param {number} dim2 - второе измерение ящика (мм)
       * @returns {object} - результат укладки
       */
      function calculateSimpleLayout(dim1, dim2) {
        // Пробуем оба варианта ориентации
        const variant1 = {
          alongLength: Math.floor(PALLET.length / dim1),
          alongWidth: Math.floor(PALLET.width / dim2)
        };
        variant1.total = variant1.alongLength * variant1.alongWidth;

        const variant2 = {
          alongLength: Math.floor(PALLET.length / dim2),
          alongWidth: Math.floor(PALLET.width / dim1)
        };
        variant2.total = variant2.alongLength * variant2.alongWidth;

        // Выбираем лучший вариант
        if (variant1.total >= variant2.total) {
          return {
            total: variant1.total,
            alongLength: variant1.alongLength,
            alongWidth: variant1.alongWidth,
            details: `${variant1.alongLength} × ${variant1.alongWidth}`
          };
        } else {
          return {
            total: variant2.total,
            alongLength: variant2.alongLength,
            alongWidth: variant2.alongWidth,
            details: `${variant2.alongLength} × ${variant2.alongWidth}`
          };
        }
      }

      /**
       * Проверяет возможность улучшенной комбинированной укладки
       * Используется только когда есть явная возможность добавить ящики
       */
      function tryOptimizedCombinedLayout(boxLength, boxWidth) {
        const layouts = [];

        // Базовые простые укладки
        const simple1 = calculateSimpleLayout(boxLength, boxWidth);
        layouts.push({
          ...simple1,
          type: 'simple'
        });

        // Пробуем комбинированную укладку только если есть значительное свободное пространство
        // Вариант 1: максимум вдоль длины, остаток заполняем поперечными
        const alongLength = Math.floor(PALLET.length / boxLength);
        const usedLength = alongLength * boxLength;
        const remainingLength = PALLET.length - usedLength;

        if (remainingLength >= boxWidth) {
          const alongWidth1 = Math.floor(PALLET.width / boxWidth);
          const crosswise = Math.floor(remainingLength / boxWidth);
          const alongWidth2 = Math.floor(PALLET.width / boxLength);

          const total = (alongLength * alongWidth1) + (crosswise * alongWidth2);

          if (total > simple1.total) {
            layouts.push({
              total,
              alongLength,
              alongWidth: alongWidth1,
              details: `${alongLength}×${alongWidth1} + ${crosswise}×${alongWidth2} поперек`,
              type: 'combined'
            });
          }
        }

        // Вариант 2: максимум вдоль ширины, остаток заполняем
        const alongWidth = Math.floor(PALLET.width / boxWidth);
        const usedWidth = alongWidth * boxWidth;
        const remainingWidth = PALLET.width - usedWidth;

        if (remainingWidth >= boxLength) {
          const alongLength1 = Math.floor(PALLET.length / boxLength);
          const crosswiseRows = Math.floor(remainingWidth / boxLength);
          const alongLength2 = Math.floor(PALLET.length / boxWidth);

          const total = (alongLength1 * alongWidth) + (alongLength2 * crosswiseRows);

          if (total > simple1.total) {
            layouts.push({
              total,
              alongLength: alongLength1,
              alongWidth,
              details: `${alongLength1}×${alongWidth} + ${alongLength2}×${crosswiseRows} поперек`,
              type: 'combined'
            });
          }
        }

        // Возвращаем лучший вариант
        layouts.sort((a, b) => b.total - a.total);
        return layouts[0];
      }

      /**
       * Анализирует все возможные положения ящика на паллете
       */
      function analyzeAllOrientations(boxLength, boxWidth, boxHeight) {
        const orientations = [];

        // 1. Стандартное положение (высота вверх)
        const standard = tryOptimizedCombinedLayout(boxLength, boxWidth);
        orientations.push({
          name: standard.type === 'combined' ? 'Стандартное комбинированное' : 'Стандартное',
          boxesPerLayer: standard.total,
          layerHeight: boxHeight,
          details: standard.details,
          stability: 'высокая'
        });

        // 2. На боку: длина × высота основание, ширина вверх
        const side1 = tryOptimizedCombinedLayout(boxLength, boxHeight);
        orientations.push({
          name: side1.type === 'combined' ? 'На боку комбинированное (ширина вверх)' : 'На боку (ширина вверх)',
          boxesPerLayer: side1.total,
          layerHeight: boxWidth,
          details: side1.details,
          stability: 'средняя'
        });

        // 3. На боку: ширина × высота основание, длина вверх
        const side2 = tryOptimizedCombinedLayout(boxWidth, boxHeight);
        orientations.push({
          name: side2.type === 'combined' ? 'На боку комбинированное (длина вверх)' : 'На боку (длина вверх)',
          boxesPerLayer: side2.total,
          layerHeight: boxLength,
          details: side2.details,
          stability: 'низкая'
        });

        // 4. Перевернутый: высота × ширина основание, длина вверх
        const flip1 = tryOptimizedCombinedLayout(boxHeight, boxWidth);
        orientations.push({
          name: flip1.type === 'combined' ? 'Перевернутый комбинированное (длина вверх)' : 'Перевернутый (длина вверх)',
          boxesPerLayer: flip1.total,
          layerHeight: boxLength,
          details: flip1.details,
          stability: 'низкая'
        });

        // 5. Перевернутый: высота × длина основание, ширина вверх
        const flip2 = tryOptimizedCombinedLayout(boxHeight, boxLength);
        orientations.push({
          name: flip2.type === 'combined' ? 'Перевернутый комбинированное (ширина вверх)' : 'Перевернутый (ширина вверх)',
          boxesPerLayer: flip2.total,
          layerHeight: boxWidth,
          details: flip2.details,
          stability: 'средняя'
        });

        return orientations;
      }

      /**
       * Основная функция расчета оптимальной укладки
       */
      function calculateOptimalPalletLoading(maxPalletHeight, boxLength, boxWidth, boxHeight, boxWeight, allowUnstable = false) {
        const availableHeight = maxPalletHeight - PALLET.height;

        const orientations = analyzeAllOrientations(boxLength, boxWidth, boxHeight);

        // Фильтруем по стабильности
        const viableOrientations = allowUnstable
            ? orientations
            : orientations.filter(o => o.stability !== 'низкая');

        let bestOption = null;
        let maxBoxes = 0;

        viableOrientations.forEach(orientation => {
          const layers = Math.floor(availableHeight / orientation.layerHeight);

          if (layers > 0) {
            const totalBoxes = orientation.boxesPerLayer * layers;

            if (totalBoxes > maxBoxes) {
              maxBoxes = totalBoxes;
              bestOption = {
                orientation: orientation.name,
                boxesPerLayer: orientation.boxesPerLayer,
                layerDetails: orientation.details,
                layers: layers,
                totalBoxes: totalBoxes,
                totalWeight: (totalBoxes * boxWeight).toFixed(2),
                layerHeight: orientation.layerHeight,
                totalHeight: (PALLET.height + layers * orientation.layerHeight).toFixed(0),
                stability: orientation.stability
              };
            }
          }
        });

        const allOptions = viableOrientations.map(orientation => {
          const layers = Math.floor(availableHeight / orientation.layerHeight);
          const totalBoxes = orientation.boxesPerLayer * layers;

          return {
            orientation: orientation.name,
            boxesPerLayer: orientation.boxesPerLayer,
            layerDetails: orientation.details,
            layers: layers,
            totalBoxes: totalBoxes,
            totalWeight: (totalBoxes * boxWeight).toFixed(2),
            layerHeight: orientation.layerHeight,
            totalHeight: layers > 0 ? (PALLET.height + layers * orientation.layerHeight).toFixed(0) : 0,
            stability: orientation.stability
          };
        }).sort((a, b) => b.totalBoxes - a.totalBoxes);

        return {
          best: bestOption,
          allOptions: allOptions
        };
      }

      /**
       * Форматированный вывод результата
       */
      /*function formatResult(result) {
        if (!result.best) {
          return "Невозможно разместить ящики на паллете с указанными параметрами";
        }

        let output = "=== ОПТИМАЛЬНЫЙ ВАРИАНТ УКЛАДКИ ===\n\n";
        output += `Ориентация: ${result.best.orientation}\n`;
        output += `Стабильность: ${result.best.stability}\n`;
        output += `Ящиков в слое: ${result.best.boxesPerLayer} (${result.best.layerDetails})\n`;
        output += `Количество слоев: ${result.best.layers}\n`;
        output += `✓ Всего ящиков на паллете: ${result.best.totalBoxes} шт.\n`;
        output += `✓ Общий вес паллеты: ${result.best.totalWeight} кг\n`;
        output += `Итоговая высота: ${result.best.totalHeight} мм\n\n`;

        output += "=== ВСЕ ВАРИАНТЫ (по убыванию эффективности) ===\n\n";
        result.allOptions.forEach((opt, index) => {
          if (opt.totalBoxes > 0) {
            output += `${index + 1}. ${opt.orientation} [${opt.stability}]\n`;
            output += `   Ящиков в слое: ${opt.boxesPerLayer} | Слоев: ${opt.layers} | Всего: ${opt.totalBoxes} шт. | Вес: ${opt.totalWeight} кг\n\n`;
          }
        });

        return output;
      }*/

        let result = calculateOptimalPalletLoading(
            this.maxPalletHeight,
            this.boxLength,
            this.boxWidth,
            this.boxHeight,
            this.boxWeight,
            this.allowUnstable);

      this.bestResult = result;
      this.allResults = result.allOptions;
      this.isCalculated = true;

    },
    clearCalculations(){
      this.isCalculated = false;

      this.maxPalletHeight = null;
      this.boxLength = null;
      this.boxWidth = null;
      this.boxHeight = null;
      this.boxWeight = null;
    },
    handleEnter(event) {
      if (event.key === 'Enter') {
        this.checkFields();
      } else if (event.key === 'Escape') {
        this.clearCalculations();
      }
    },
  },
  mounted() {
    localStorage.setItem('checkLoadStatus', 'loaded');
    document.addEventListener('keydown', this.handleEnter);
  },
  beforeUnmount() {
    localStorage.removeItem('checkLoadStatus');
    document.removeEventListener('keydown', this.handleEnter);
  }
}
</script>

<style lang="scss" scoped>

  .cargo-calculations-container{
    width: 90%;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    background-color: snow;
    overflow: hidden;
    margin: 0 auto;

    h2{
      text-transform: uppercase;
      text-align: left;
      margin: 30px;
      color: gray;
      font-size: clamp(1rem, 2vw, 1.2rem);
    }

    .v-divider{
      width: 80%;
      margin: 0 auto;
    }

    .my-calculate-form{
      padding: 5px;
      margin: 20px auto;

      .inputs{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        flex-wrap: wrap;
        gap: 10px;
        max-width: 1000px;
        margin: 0 auto;

        .inpt{
          width: 280px;
          max-width: 280px;
        }
      }

      .apply-calculations{
        margin: 3px 10px;
      }
    }

    .results{
      max-width: 700px;
      margin: 0 auto;

      .optimal-title{
        margin-top: 20px;
        color: green;
        display: none;
      }

      .optimal-result{
        padding: 5px;
        margin: 0 auto;
        display: none;

        .best-wrapper{

          .wrapper{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
            margin: 0 auto;
            border-bottom: 1px solid rgba(128, 128, 128, 0.5);

            .info{
              font-weight: bold;
            }

            .data{
              color: gray;
              font-weight: bold;
            }
          }
        }
      }

      .all-title{
        margin-top: 30px;
        color: orange;
        display: none;
      }

      .all-result{
        padding: 15px;
        margin: 20px auto 20px auto;
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
        border-radius: 10px;
        background-color: white;

        .all-result-count{
          width: 100%;
          margin: 3px 0 5px 0;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          gap: 10px;

          .variant{
            color: coral;
            font-weight: bold;
            text-transform: uppercase;
          }

          .variant-logo{

          }
        }

        .best-wrapper{

          .wrapper{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
            margin: 3px auto;
            border-bottom: 1px solid rgba(128, 128, 128, 0.5);

            .info{
              font-weight: bold;
              text-transform: uppercase;
              font-size: 14px;
            }

            .data{
              color: gray;
              font-weight: bold;
              text-transform: lowercase;
            }
          }
        }
      }
    }
  }

</style>